
/*==============================================================
*												COLS PADDINGS
=============================================================== */

@each $col in $cols {
  @each $size in $sizes {
    .col-#{$size}-#{$col} {
      padding-left: $col-xs-p;
      padding-right: $col-xs-p;
      .list-row & {
        padding-bottom: $col-xs-p * 2;
      }
    }
    @media (min-width: $min_sm) {
      .col-#{$size}-#{$col} {
        padding-left: $col-sm-p;
        padding-right: $col-sm-p;
        .list-row & {
          padding-bottom: $col-sm-p * 2;
        }
      }
    }
    @media (min-width: $min_md) {
      .col-#{$size}-#{$col} {
        padding-left: $col-md-p;
        padding-right: $col-md-p;
        .list-row & {
          padding-bottom: $col-md-p * 2;
        }
      }
    }
    @media (min-width: $min_ml) {
      .col-#{$size}-#{$col} {
        padding-left: $col-ml-p;
        padding-right: $col-ml-p;
        .list-row & {
          padding-bottom: $col-ml-p * 2;
        }
      }
    }
    @media (min-width: $min_lg) {
      .col-#{$size}-#{$col} {
        padding-left: $col-lg-p;
        padding-right: $col-lg-p;
        .list-row & {
          padding-bottom: $col-lg-p * 2;
        }
      }
    }
    @media (min-width: $min_xl) {
      .col-#{$size}-#{$col} {
        padding-left: $col-xl-p;
        padding-right: $col-xl-p;
        .list-row & {
          padding-bottom: $col-xl-p * 2;
        }
      }
    }
  }
}

/*==============================================================
*										    	COLS FULL WIDTH
=============================================================== */

@media (max-width: $max_xs) {
  @each $col in $cols {
    .col-xs-#{$col} {
      width: 100%;
    }
  }
}
@media (max-width: $max_sm) {
  @each $col in $cols {
    .col-sm-#{$col} {
      width: 100%;
    }
  }
}
@media (max-width: $max_md) {
  @each $col in $cols {
    .col-md-#{$col} {
      width: 100%;
    }
  }
}
@media (max-width: $max_ml) {
  @each $col in $cols {
    .col-ml-#{$col} {
      width: 100%;
    }
  }
}
@media (max-width: $max_lg) {
  @each $col in $cols {
    .col-lg-#{$col} {
      width: 100%;
    }
  }
}
@media (max-width: $max_xl) {
  @each $col in $cols {
    .col-xl-#{$col} {
      width: 100%;
    }
  }
}

/*==============================================================
*											COLS RESPONSIVE WIDTH
=============================================================== */

@media (min-width: $min_xs) {
  @each $col in $cols {
    .col-xs-#{$col} {
      width: 100% / 12 * $col;
    }
  }
}
@media (min-width: $min_sm) {
  @each $col in $cols {
    .col-sm-#{$col} {
      width: 100% / 12 * $col;
    }
  }
}
@media (min-width: $min_md) {
  @each $col in $cols {
    .col-md-#{$col} {
      width: 100% / 12 * $col;
    }
  }
}
@media (min-width: $min_ml) {
  @each $col in $cols {
    .col-ml-#{$col} {
      width: 100% / 12 * $col;
    }
  }
}
@media (min-width: $min_lg) {
  @each $col in $cols {
    .col-lg-#{$col} {
      width: 100% / 12 * $col;
    }
  }
}
@media (min-width: $min_xl) {
  @each $col in $cols {
    .col-xl-#{$col} {
      width: 100% / 12 * $col;
    }
  }
}

/*==============================================================
*												CONTAINERS / ROWS
=============================================================== */

.container,
.container-fluid {
  width: 100%;
  padding-left: $cont-xs-p;
  padding-right: $cont-xs-p;
  margin-left: auto;
  margin-right: auto;
  &:not(.container-fluid) {
    max-width: $cont-xs-width;
  }

  @media (min-width: $min_sm) {
    padding-left: $cont-sm-p;
    padding-right: $cont-sm-p;
    &:not(.container-fluid) {
      max-width: $cont-sm-width;
    }
  }
  @media (min-width: $min_md) {
    padding-left: $cont-md-p;
    padding-right: $cont-md-p;
    &:not(.container-fluid) {
      max-width: $cont-md-width;
    }
  }
  @media (min-width: $min_ml) {
    padding-left: $cont-ml-p;
    padding-right: $cont-ml-p;
    &:not(.container-fluid) {
      max-width: $cont-ml-width;
    }
  }
  @media (min-width: $min_lg) {
    padding-left: $cont-lg-p;
    padding-right: $cont-lg-p;
    &:not(.container-fluid) {
      max-width: $cont-lg-width;
    }
  }
  @media (min-width: $min_xl) {
    padding-left: $cont-xl-p;
    padding-right: $cont-xl-p;
    &:not(.container-fluid) {
      max-width: $cont-xl-width;
    }
  }
}
.row {
  display: flex;
  align-items: stretch;
  flex-wrap: wrap;

  margin-left: -#{$col-xs-p};
  margin-right: -#{$col-xs-p};
  &.list-row {
    margin-bottom: -#{$col-xs-p * 2};
  }

  @media (min-width: $min_sm) {
    margin-left: -#{$col-sm-p};
    margin-right: -#{$col-sm-p};
    &.list-row {
      margin-bottom: -#{$col-sm-p * 2};
    }
  }
  @media (min-width: $min_md) {
    margin-left: -#{$col-md-p};
    margin-right: -#{$col-md-p};
    &.list-row {
      margin-bottom: -#{$col-md-p * 2};
    }
  }
  @media (min-width: $min_ml) {
    margin-left: -#{$col-ml-p};
    margin-right: -#{$col-ml-p};
    &.list-row {
      margin-bottom: -#{$col-ml-p * 2};
    }
  }
  @media (min-width: $min_lg) {
    margin-left: -#{$col-lg-p};
    margin-right: -#{$col-lg-p};
    &.list-row {
      margin-bottom: -#{$col-lg-p * 2};
    }
  }
  @media (min-width: $min_xl) {
    margin-left: -#{$col-xl-p};
    margin-right: -#{$col-xl-p};
    &.list-row {
      margin-bottom: -#{$col-xl-p * 2};
    }
  }
}
